.Banner {
  width: 100%;
  height: 60px;
  background: #ffffff;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
          box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
  position: fixed;
  z-index: 1;
}

.Logo {
  width: 50px;
  float: left;
  margin-left: 15px;
  margin-top: 5px;
}

#eventName{
  margin-top: 66px;
}

#memberBio {
  width: 50%;
}

#teamPhotos {
  width: 50%;
}

html {
  scroll-behavior: smooth;
}

@font-face {
  font-family: Body;
  src: url('Fonts/Roboto-Light.ttf');
}

@font-face {
  font-family: Heading;
  src: url('Fonts/Roboto-Medium.ttf');
}

#heading {
  font-family: Heading;
}

a {
  text-decoration: none;
}


p,
h1 {
  font-family: Body;
}

#mobileMenuIcon {
  display: none;
}

li {
  color: black;
}

li:hover {
  text-decoration: underline;
}

#mobileMenu {
  width: 100%;
  height: 283px;
  background: #4d5da8;
  text-align: center;
  color: white;
  padding-bottom: 5px;
  padding-top: 5px;
  display: none;
  -webkit-box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
          box-shadow: 1px 1px 1px 1px rgba(0, 0, 0, 0.27058823529411763);
  position: fixed;
}

#mainBanner {
  width: 100%;
  height: 650px;
  background: url(images/background.jpg);
  background-size: cover;
  background-attachment: fixed;
}

#blueInnerBanner {
  width: 30%;
  height: 650px;
  background: rgba(77, 93, 168, 0.6784313725490196);
  text-align: center;
  color: white;
  -webkit-box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.27058823529411763);
          box-shadow: 2px 2px 2px 2px rgba(0, 0, 0, 0.27058823529411763);
  float: left;
}

button {
  width: 120px;
  height: 40px;
  background: white;
  border: 0px;
  outline: none;
  border-radius: 10px;
  cursor: pointer;
}

#mainPromoBanner {
  float: right;
  height: 300px;
  width: 500px;
  margin-top: 320px;
  margin-right: 60px;
  background: url('images/updateOne.jpg');
  background-size: cover;
}

#mainBannerHeading {
  margin-top: 200px;
}

#mainBanner:hover {
  -webkit-transform: skew(5);
      -ms-transform: skew(5);
          transform: skew(5)
}

#eventsSection {
  width: 100%;
  height: 600px;
  background: #fcfcfc;
}

#imagesContainer {
  float: left;
  margin-left: 50px;
}

#imageSelectionOne {
  width: 10px;
  height: 10px;
  margin-left: 10px;
  border-radius: 200px;
  background: #4d5da8;
  color: white;
  float: left;
  margin-top: 40px;
  padding: 10px;
  cursor: pointer;
}

#mainImageCont {
  width: 497px;
  height: 350px;
  background: #8080800d;
  margin-top: 60px;
  border: 5px solid white;
  background: url(images/C1.jpg);
  background-size: cover;
}

#imageBlock1,
#imageBlock2,
#imageBlock3,
#imageBlock4,
#imageBlock5 {
  width: 91.2px;
  height: 91.2px;
  background: #8080800d;
  margin-top: 10px;
  float: left;
  border: 1px solid white;
  cursor: pointer;
}

#imageBlock2,
#imageBlock3,
#imageBlock4,
#imageBlock5 {
  margin-left: 10px;
}

#imageBlock1 {
  background-image: url(images/C1.jpg);
  background-size: cover;
}

#imageBlock2 {
  background-image: url(images/C2.jpg);
  background-size: cover;
}

#imageBlock3 {
  background-image: url(images/C3.jpg);
  background-size: cover;
}

#imageBlock4 {
  background-image: url(images/C4.jpg);
  background-size: cover;
}

#imageBlock5 {
  background-image: url(images/C5.jpg);
  background-size: cover;
}

#eventsText {
  float: right;
  margin-right: 50px;
  text-align: left;
  width: 600px;
}

hr {
  width: 100px;
  height: 1px;
  float: left;
  border: none;
  background: rgba(0, 0, 0, 0.24);
}

#memberImage1,
#memberImage2,
#memberImage3,
#memberImage4,
#memberImage5,
#memberImage6 {
  width: 200px;
  height: 200px;
  float: left;
}

#about {
  width: 100%;
  height: 400px;
  margin-bottom: 10px;
}

#aboutRight {
  float: right;
  width: 50%;
  height: 100%;
  background: url(./images/background.jpg);
  background-size: cover;
  background-attachment: fixed;
}

#aboutLeft {
  float: left;
  width: 50%;
  height: 100%;
}

#aboutTab {
  width: 50%;
  height: 50%;
  float: left;
  -webkit-box-shadow: 0.5px 0.5px 0.5px 0.5px #8080803b;
          box-shadow: 0.5px 0.5px 0.5px 0.5px #8080803b;
  cursor: pointer;
}

#aboutTab hr {
  margin-left: 27px;
  border: 0.5px dotted rgba(0, 0, 0, 0.13);
  height: 0;
  background: unset;
}

#aboutTab h1 {
  margin: 0px;
  margin-left: 27px;
  font-size: 23px;
  font-weight: lighter;
}

#aboutTitle {
  color: white;
  font-weight: lighter;
  margin-left: 25px;
  margin-bottom: 0px;
}

#aboutLeftContent #aboutText,
#aboutLeftContent #aboutNumber {
  color: white;
  text-align: left;
  margin-left: 25px;
  background: none;
  margin-top: 20px;
  width: 481px;

}

#aboutNumbCont {
  width: 40px;
  height: 40px;
  background: white;
  margin-left: 25px;
  text-align: -webkit-center;
  text-align: -moz-center;
  border-radius: 200px;
}

#aboutLeftContent #aboutNumber {
  font-size: 24px;
  color: #E91E63;
  width: -webkit-max-content;
  width: -moz-max-content;
  width: max-content;
  margin-left: 0px;
}


#aboutLeftContent hr {
  margin-left: 25px;
  border: 0.5px dotted white;
  background: unset;
  height: 0px;
}

#aboutTab p {
  margin: 0px;
  margin-top: 60px;
  color: #E91E63;
  margin-left: 27px;
  font-weight: bold;

}

#aboutTab:hover {
  -webkit-box-shadow: 5px 5px 5px 5px #8080803b;
          box-shadow: 5px 5px 5px 5px #8080803b;
}

#Aboutoverlay {
  width: 100%;
  height: 100%;
  background: rgba(77, 93, 168, 0.6784313725490196);
}

#aboutLeftContent p {
  margin: 0px;
  color: rgb(255, 255, 255);
  font-size: 18px;
  background: rgb(233, 30, 99);
  width: 285px;
  padding: 5px;
}

#aboutText span {
  font-size: 15px;
}

#committeDiv {
  width: 100%;
  height: 350px;
  margin-top: 30px;
}

#committerLeftCont {
  float: left;
  margin-left: 50px;
}

#committeeRightCont {
  float: right;
  margin-right: 50px;
}

#memberProfile {
  width: 100%;
  height: 205px;
  background: rgb(58, 68, 119);
  margin-top: 128px;
  color: white;
}

#memberName {
  font-size: 21px;
  color: rgb(16, 189, 189);
  margin-left: 15px;
  padding-top: 8px;
  margin-bottom: 0px;
}

#role {
  margin-left: 15px;
  margin-top: 0px;
  font-size: 15px;
}

#responsibilities {
  font-size: 13px;
  margin-left: 15px;
}

#faceBookIcon,
#InstagramIcon,
#twitterIcon {
  margin-top: 20px;
  float: left;
  cursor: pointer;
}

#InstagramIcon,
#twitterIcon {
  margin-left: 15px;
}

#twitterIcon {
  margin-top: 25px;
}

#committeeHeading {
  font-size: 25px;
  font-weight: lighter;
}

#teamIcon {
  margin-top: 20px;
}

#Tumi {
  width: 100px;
  height: 100px;
  background: white;
  border-radius: 100px;
  margin-right: 15px;
  float: right;
  -webkit-box-shadow: 1px 1px 1px 1px #8080804d;
          box-shadow: 1px 1px 1px 1px #8080804d;
  background-image: url(./images/tumi.jpg);
  background-size: cover;
  border-top-right-radius: 0px;
  cursor: pointer;
}

#Dembe {
  width: 100px;
  height: 100px;
  background: white;
  border-radius: 100px;
  float: right;
  margin-right: 15px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 1px 1px #8080804d;
          box-shadow: 1px 1px 1px 1px #8080804d;
  background-image: url(./images/dembe.jpg);
  background-size: cover;
}

#Tatenda {
  width: 100px;
  height: 100px;
  background: white;
  border-radius: 100px;
  float: right;
  margin-right: 15px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 1px 1px #8080804d;
          box-shadow: 1px 1px 1px 1px #8080804d;
  background-image: url(./images/tatenda.jpg);
  background-size: cover;
}

#Tsego {
  width: 100px;
  height: 100px;
  background: white;
  border-radius: 100px;
  float: right;
  margin-right: 15px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 1px 1px #8080804d;
          box-shadow: 1px 1px 1px 1px #8080804d;
  background-image: url(./images/tsego.jpg);
  background-size: cover;
}

#Joey {
  width: 100px;
  height: 100px;
  background: white;
  border-radius: 100px;
  float: right;
  margin-right: 15px;
  cursor: pointer;
  -webkit-box-shadow: 1px 1px 1px 1px #8080804d;
          box-shadow: 1px 1px 1px 1px #8080804d;
  background-image: url(./images/joey.jpg);
  background-size: cover;
}


#footer {
  width: 100%;
  height: 40px;
  background: rgb(58, 68, 119);
}

#footerSocialIcon {
  float: right;
  margin-right: 20px;
}

#footercontact {
  float: left;
  margin-left: 15px;
  color: white;
}

#footercontact p {
  margin: 0px;
  margin-top: 10px;
  font-size: 13px;
}


::-webkit-scrollbar {
  width: 5px;
  background: #4d5da8;
}

::-webkit-scrollbar-thumb {
  background: #10bdbd;
  border-radius: 40px;
}

#eventsIcon {
  width: 100px;
  margin-top: 30px;
}




/*Responssiveness*/
@media (max-width:1600px) {

  #mainPromoBanner,
  #eventsText,
  #committeeRightCont {
    margin-right: 150px;
  }

  #imagesContainer,
  #committerLeftCont {
    margin-left: 150px;
  }
}

@media(max-width:1550px) {
  #mainPromoBanner {
    margin-right: 60px;
  }

  #eventsText,
  #committeeRightCont {
    margin-right: 50px;
  }

  #imagesContainer,
  #committerLeftCont {
    margin-left: 50px;
  }
}

@media (max-width:1250px) {

  #aboutLeftContent #aboutText{
    padding: 0px;
    margin-left: 5px;
    margin-right: 5px;
    width: 97%;
  }

  #aboutTitle{
    width: 97%;
    margin-left: 5px;
  }

  #Aboutoverlay hr{
    margin-left: 5px;
  }

  #mobileMenuIcon {
    display: block;
  }

  #eventName{
    margin-top: 60px;
  }


  #footer {
    float: left;
    height: auto;
    padding-bottom: 20px;
  }

  #committerLeftCont {
    float: none;
    margin-left: 15px;
  }

  #committeeRightCont {
    float: none;
    margin-right: 0px;
  }

  #memberImages {
    height: auto;
    margin-top: 10px;
  }

  #memberProfile {
    margin-top: 20px;
    float: left;
    height: auto;
    padding-bottom: 20px;
  }

  #committeDiv {
    height: auto;
    margin-top: 20px;
    float: left;
  }

  #memberImages {
    height: auto;
    margin-top: 10px;
    width: 100%;
    float: left;
  }

  #memberImages div {
    margin-top: 20px;
  }

  #aboutRight,
  #aboutLeft {
    float: none;
    width: 100%;
  }

  #desktopmenu {
    display: none;
  }

  #blueInnerBanner {
    width: 100%;
    height: 380px;
    float: none;
  }

  #mainPromoBanner {
    float: none;
    width: 100%;
    margin-top: 0px;
  }

  li {
    color: white;
  }

  #mainBannerHeading {
    margin-top: 70px;
  }

  #mainImageCont {
    width: 100%;
    border: 0px;
  }

  #imageBlock2,
  #imageBlock3,
  #imageBlock4,
  #imageBlock5 {
    margin-left: 0px;
  }

  #imagesContainer {
    margin-left: 0px;
  }

  #eventsText {
    width: unset;
    margin-left: 15px;
  }

  #eventsSection {
    height: 1000px;
  }
}

@media (max-width:600px) {

  #eventsSection {
    height: 1100px;
  }

}

@media (max-width:476px) {

  #eventsSection {
    height: 1250px;
  }

}

@media (max-width:366px) {

  #eventsSection {
    height: 1350px;
  }

}


